.time-left {
	position: absolute;
	top: 20px;
	right: 120px;
	padding: 10px;
	padding-left: 48px;
	background-color: #e6e6e6;
	@include respond-to(tablets) {
		right: 20px;
		top: 14px;
	}
	&__text {
		display: block;
		font-family: 'HelveticaNeue', Arial, sans-serif;
		font-size: 12px;
		color: #000;
	}
	&__time {
		display: block;
		font-family: 'HelveticaNeue', Arial, sans-serif;
		font-size: 14px;
		color: #f68235;
	}
	&.icon--clock {
		&::before {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			font-size: 28px;
			color: #898989;
			left: 10px;
		}
	}
}

.product {
	display: flex;
	margin-top: 40px;
	&__title {
		font-size: 25px;
		color: #f68235;
		margin: 0;
		font-weight: 300;
	}
	&__subtitle {
		font-size: 18px;
		font-weight: 200;
		color: #000;
		margin: 0;
		display: block;
		@include respond-to(tablets) {
			padding-right: 90px;
		}
	}
}

.photo {
	width: 60%;
	display: flex;
	justify-content: space-between;
	@include respond-to(tablets) {
		flex-direction: column;
		justify-content: flex-start;
	}
	&__view {
		width: 481px;
		height: 641px;
		background: url('../images/photo-view-min.jpg') no-repeat;
		@include respond-to(tablets) {
			order: 1;
			width: 414px;
			height: 553px;
			background: url('../images/photo-view-tab-min.jpg') no-repeat;
		}
	}
	&__nav {
		display: flex;
		flex-direction: column;
		@include respond-to(tablets) {
			order: 2;
			flex-direction: row;
			margin-top: 24px;
			height: 91px;
			.prev {
				height: 1px;
				width: 16px;
				margin: 50px 30px;
				&::before {
					left: 1px;
					top: -5px;
					transform: rotate(-135deg);
				}
			}
			.next {
				height: 1px;
				width: 16px;
				margin: 50px 30px;
				&::before {
					left: 1px;
					top: -5px;
					transform: rotate(45deg);
				}
			}
		}
		.prev {
			position: relative;
			width: 1px;
			height: 16px;
			background-color: #d0d0d0;
			margin: 0 auto;
			margin-bottom: 5px;
			&::before {
				content: '';
				position: absolute;
				left: -5px;
				top: 1px;
				width: 10px;
				height: 10px;
				border-top: 1px solid #d0d0d0;
				border-right: 1px solid #d0d0d0;
				transform: rotate(-45deg);
			}
		}
		&-item {
			margin: 10px auto;
			border: 2px solid transparent;
			width: 62px;
			height: 91px;
			display: block;
			&-jpg {
				width: 100%;
				height: auto;
			}
			&.active {
				border: 2px solid #f68235;
			}
			&:hover {
				cursor: pointer;
				border: 2px solid #999;
			}
		}
		.next {
			position: relative;
			width: 1px;
			height: 16px;
			background-color: #f68235;
			margin: 0 auto;
			margin-bottom: 5px;
			&::before {
				content: '';
				position: absolute;
				left: -5px;
				top: 1px;
				width: 10px;
				height: 10px;
				border-top: 1px solid #f68235;
				border-right: 1px solid #f68235;
				transform: rotate(135deg);
			}
		}
	}
}

.description {
	width: 40%;
}

.price {
	margin-top: 15px;
	font-size: 25px;
	margin-bottom: 30px;
	&--old {
		text-decoration: line-through;
		color: #999;
		margin-right: 15px;
		@include respond-to(tablets) {
			margin: 0;
			display: block;
			font-size: 14px;
		}
	}
	&--new {
		color: #f68235;
		@include respond-to(tablets) {
			display: block;
			font-size: 24px;
		}
	}
}

.color {
	border-top: 1px solid #dedede;
	padding: 14px 0;
	font-size: 14px;
	@include respond-to(tablets) {
		font-size: 12px;
	}
	&__text {
		font-weight: 600;
		display: inline-block;
		margin-bottom: 10px;
		margin-right: 5px;
	}
	&__pic {
		width: 35px;
		height: 35px;
		display: inline-block;
		margin-right: 10px;
		&.active {
			border: 2px solid #f68235;
		}
		&:hover {
			border: 2px solid #fff;
			cursor: pointer;
		}
	}
	&--1 {
		background: url(../images/color1-min.png) no-repeat;
	}
	&--2 {
		background: url(../images/color2-min.png) no-repeat;
	}
	&--3 {
		background: url(../images/color3-min.png) no-repeat;
	}
	&--4 {
		background: url(../images/color4-min.png) no-repeat;
	}
	&--5 {
		background: url(../images/color5-min.png) no-repeat;
	}
}

.size {
	border-top: 1px solid #dedede;
	padding: 14px 0;
	font-size: 14px;
	@include respond-to(tablets) {
		font-size: 12px;
	}
	&__info {
		display: flex;
		justify-content: space-between;
	}
	&__text {
		font-weight: 600;
		margin-bottom: 10px;
	}
	&__table {
		color: #999;
		&:hover {
			color: #000;
		}
	}
	&__pic {
		width: 35px;
		height: 35px;
		display: inline-block;
		margin-right: 10px;
		border: 1px solid #ccc;
		color: #000;
		text-align: center;
		padding: 8px;
		@include respond-to(tablets) {
			padding: 9px;
		}
		&.active {
			border: 2px solid #f68235;
		}
		&.disabled {
			color: #6d6d6d;
			background-color: #dedede;
			pointer-events: none;
		}
		&:hover {
			border: 2px solid #000;
			cursor: pointer;
		}
	}
	&__fit {
		margin-top: 15px;
		color: #999;
		display: inline-block;
		&:hover {
			color: #000;
		}
		span {
			font-family: 'OpenSans', sans-serif;
			font-size: 14px;
			@include respond-to(tablets) {
				font-size: 12px;
			}
		}
		&.icon--hanger {
			position: relative;
			&::before {
				position: absolute;
				font-size: 13px;
				background-color: #487eba;
				width: 14px;
				height: 14px;
				border-radius: 100%;
				color: #fff;
				left: 100%;
				margin-left: 10px;
				top: 50%;
				transform: translateY(-50%);
				padding: 5px;
				text-align: center;
				@include respond-to(tablets) {
					font-size: 10px;
					width: 12px;
					height: 12px;
					padding: 3px;
				}
			}
		}
	}
}

.quantity {
	border-top: 1px solid #dedede;
	padding: 14px 0;
	font-size: 14px;
	&__text {
		font-weight: 600;
	}
	&__count {
		margin-top: 10px;
		&-less {
			display: inline-block;
			position: relative;
			&::before {
				content: "";
				position: absolute;
				top: 50%;
				transform: translate(-50%);
				width: 9px;
				height: 2px;
				background-color: #000;
			}
			margin-right: 8px;
			margin-bottom: 4px;
			cursor: pointer;
		}
		&-number {
			border: 1px solid #dedede;
			text-align: center;
			color: #000;
			font-family: 'OpenSans', sans-serif;
			font-weight: 600;
			display: inline-block;
			outline: none;
			width: 76px;
			height: 35px;
			@include respond-to(tablets) {
				padding: 9px;
				width: 35px;
				height: 35px;
			}
		}
		&-more {
			display: inline-block;
			margin-left: 8px;
			&::before {
				position: absolute;
				content: "";
				top: 50%;
				transform: translate(-50%);
				width: 9px;
				height: 2px;
				background-color: #000;
			}
			position: relative;
			margin-bottom: 4px;
			cursor: pointer;
			&::after {
				content: "";
				position: absolute;
				width: 2px;
				height: 10px;
				top: -4px;
				left: 50%;
				transform: translateX(-50%);
				background-color: #000;
			}
		}
	}
	&__left {
		position: absolute;
		top: -54px;
		left: 0;
		background-color: #eaeaea;
		padding: 7px;
		text-align: center;
		color: #000;
		@include respond-to(tablets) {
			font-size: 12px;
			width: 90%;
			left: 10%;
		}
		&-number {
			margin-right: 5px;
		}
		&::after {
			content: "";
			position: absolute;
			top: 100%;
			left: 50%;
			transform: translateX(-50%);
			border: 11px solid transparent;
			border-top: 11px solid #eaeaea;
			@include respond-to(tablets) {
				left: 75%;
			}
		}
	}
}

.like {
	border: 1px solid #dedede;
	display: inline-block;
	padding: 10px 30px;
	padding-left: 50px;
	font-size: 12px;
	font-weight: 200;
	margin-top: 14px;
	margin-bottom: 14px;
	vertical-align: bottom;
	text-decoration: none;
	@include respond-to(tablets) {
		padding: 18px;
	}
	&:hover {
		border-color: #000;
	}
	&.icon--heart {
		position: relative;
		&::before {
			font-size: 13px;
			color: #000;
			position: absolute;
			left: 28px;
			top: 50%;
			transform: translateY(-50%);
			@include respond-to(tablets) {
				left: 50%;
				transform: translate(-50%, -50%);
			}
		}
	}
	span {
		text-transform: uppercase;
		color: #000;
		font-family: 'HelveticaNeue', Arial, sans-serif;
		@include respond-to(tablets) {
			display: none;
		}
	}
}

.add-to-cart {
	display: inline-block;
	padding-left: 30px;
	font-size: 13px;
	font-weight: 200;
	margin-top: 14px;
	margin-left: 19px;
	background-color: #f68235;
	vertical-align: bottom;
	position: relative;
	margin-bottom: 14px;
	text-decoration: none;
	@include respond-to(tablets) {
		padding-left: 15px;
		margin-left: 0;
		float: right;
	}
	&:hover {
		background-color: #ef590e;
	}
	&:visited {
		color: #000;
	}
	&__text {
		text-transform: uppercase;
		color: #fff;
		background: url('../images/shop-bag-min.png') no-repeat;
		background-position: 0 9px;
		padding: 12px 25px;
		display: inline-block;
		@include respond-to(tablets) {
			padding: 12px 15px;
			padding-left: 20px;
		}
	}
}

.shop-bag-img {
	height: 19px;
	width: 15px;
	margin-right: 12px;
}

.make-order {
	border-top: 1px solid #dedede;
	padding: 14px 0;
	font-size: 14px;
	@include respond-to(tablets) {
		font-size: 12px;
	}
}

.call {
	margin-bottom: 10px;
	&.icon--phone {
		position: relative;
		&::before {
			position: absolute;
			font-size: 22px;
			color: #7c7c7c;
			left: 0;
			top: 0;
		}
	}
	&__text {
		display: block;
		color: #000;
		padding-left: 30px;
		font-family: 'HelveticaNeue', Arial, sans-serif;
		margin-bottom: 10px;
	}
	&__number {
		&:nth-child(2) {
			padding-left: 25px;
		}
		color: #999;
		margin-left: 10px;
		margin-right: 10px;
		font-family: 'HelveticaNeue', Arial, sans-serif;
		&-text {
			color: #999;
			font-family: 'HelveticaNeue', Arial, sans-serif;
		}
		&:hover {
			color: #000;
		}
	}
}

.location {
	&.icon--location-pin {
		position: relative;
		&::before {
			position: absolute;
			font-size: 22px;
			color: #7c7c7c;
			left: 0;
			top: 50%;
			transform: translateY(-50%);
		}
	}
	&__text {
		display: block;
		color: #000;
		padding-left: 30px;
		font-family: 'HelveticaNeue', Arial, sans-serif;
		margin-bottom: 10px;
	}
}

.details {
	border-top: 1px solid #dedede;
	padding: 20px 0;
	&__title {
		margin: 0;
		font-weight: 300;
		font-size: 25px;
		margin-bottom: 25px;
		position: relative;
		&:hover {
			cursor: pointer;
		}
		@include respond-to(tablets) {
			font-size: 18px;
			margin-bottom: 18px;
		}
		&.icon--plus {
			& ~ .details__item, 
			& ~ .details__code, 
			& ~ .details__info,
			& ~ .details__text {
				display: none;
			}
		}
		&::before {
			position: absolute;
			right: 0;
			top: 50%;
			transform: translateY(-50%);
		}
		span {
			font-family: 'HelveticaNeue', Arial, sans-serif;
		}
	}
	&__item {
		font-size: 14px;
		@include respond-to(tablets) {
			font-size: 12px;
		}
		&-title {
			font-weight: 600;
			margin-right: 5px;
			display: inline-block;
			vertical-align: top;
		}
		&-value {
			display: inline-block;
			width: 80%;
			margin: 0;
		}
		b {
			font-weight: bold;
			margin-right: 5px;
		}
	}
	&__code {
		margin-top: 25px;
		font-size: 14px;
		@include respond-to(tablets) {
			font-size: 12px;
			margin-top: 14px;
		}
		&-title {
			font-weight: 600;
			margin-right: 5px;
			display: inline-block;
			vertical-align: top;
		}
		&-number {
			color: #a7a7a7;
			display: inline-block;
			width: 75%;
			margin: 0;
		}
	}
	&__info {
		margin-top: 20px;
	}
	&__text {
		margin: 0;
		font-size: 14px;
		@include respond-to(tablets) {
			font-size: 12px;
			margin: 0;
		}
		b {
			font-weight: bold;
			display: inline-block;
			margin-top: 20px;
			a {
				color: #000;
			}
		}
		&-item {
			display: block;
			padding-right: 30px;
		}
	}
}

.social {
	border-top: 1px solid #dedede;
	padding: 24px 0;
	display: flex;
	justify-content: flex-end;
	@include respond-to(tablets) {
		justify-content: space-between;
	}
	&__text {
		font-size: 14px;
		font-weight: 600;
	}
	&__item {
		margin-left: 20px;
		color: #3a3a3a;
		text-decoration: none;
		padding: 5px;
		border: 1px solid transparent;
		&:hover {
			border: 1px solid #3a3a3a;
			border-radius: 100%;
			padding: 5px;
		}
	}
}